body {
  max-width: 1440px;
  position: relative;
  background-image: url(../img/back.png);
  background-repeat: no-repeat;
  height: 100vh;
  background-size: 50%;
  max-width: 100%;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}

img {
  max-width: 100%;
}

.yellow_part {
  display: flex;
  justify-content: space-between;
  background-color: #f4e63c;
  margin-bottom: 50px;
  width: 100%;
  height: 100%;
  opacity: 0.9;
}

.mobile_content {
  display: none;
}

section.wrap {
  position: relative;
}

.limited_wrapper {
  position: relative;
  display: flex;

  .top_symbol {
    margin-top: 38px;
    margin-left: 32px;
  }
}

.left_col {

  display: flex;
  flex-wrap: wrap;
  margin-top: 88px;
  background-image: url(../img/left.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  max-width: 100%;
  border: 1px solid #f4e63c;
}

.peremennie {
  margin-left: 70px;
  margin-top: 70px;

  span {
    display: block;
    font-size: 14px;
    padding-bottom: 50px;
    font-family: 'Geometria';
    font-weight: 200;
  }
}

.flex_hor {
  display: flex;
  position: absolute;
  left: 154px;
  top: 133px;
  z-index: 5;
}

.horizontal_text {
  writing-mode: vertical-rl;
  text-orientation: upright;
  align-self: flex-end;

  p {
    font-size: 9px;
    font-family: "Geometria";
    font-weight: 200;
    font-style: italic;
  }
}

.horizontal_num {
  margin-top: 44px;
  margin-right: 173px;
}


.square {
  width: 180px;
}

.giometric_symbols {
  max-width: 100%;
  padding-left: 69px;
  margin-top: 70px;

}

/***************menu*/
///////
.navbar {
  margin-top: 90px;
}

.navbar-button {
  position: relative;
  display: block;
  margin-top: 42px;
}

.button {
  cursor: pointer;
  position: relative;

  i {
    font-size: 22px;
    position: absolute;
    right: -459px;
    top: -5px;
  }
}

.button:before {
  content: url(../img/button.png);
  position: absolute;
  right: -477px;
  top: -22px;
}

.overlay {
  position: fixed;
  background: #f7e611;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  overflow: hidden;
  z-index: 10000;
}

.overlay.open {
  opacity: 1;
  visibility: visible;
  height: 100%;
}

.close-menu {
  display: block;
}

.overlay.open {
  ul {
    text-align: center;
    flex-direction: column;
    padding: 0;
    margin: 190px auto;
    height: 100%;
    width: 100%;

    li {
      padding-bottom: 20px;

      a {
        text-decoration: none;
      }
    }
  }
}

/*menu/end*********************/
.top_gio {
  max-width: 100%;
  position: absolute;
  z-index: 15;
  right: 264px;
  top: 75px;
}


.symbol {
  position: absolute;
  left: 556px;
  max-width: 100%;
  z-index: 1;
  bottom: -122px;
}

.symbol:after {
  content: url("");
  position: absolute;
  width: 214px;
  height: 10px;
  background-color: rgb(247, 230, 17);
  bottom: -10px;
  left: -20px;

}

/*************right_col////////////////**/
.right_col {
  display: flex;
  flex-wrap: wrap;
  background-image: url(../img/right.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: absolute;
  left: 395px;
  top: 183px;
  z-index: 1;
  max-width: 100%;
  height: 420px;
  border: 1px solid #f4e63c;

  .top_gio {
    max-width: 100%;
    position: absolute;
    top: -100px;
    right: 121px;
    z-index: -1;
  }

  .yellow_part {
    position: relative;
    justify-content: space-around;
  }
}

.ural {
  padding: 34px 0;

  p {
    font-size: 17px;
    font-weight: 400;
    font-family: Giometria;
    color: #fff;
    padding: 15px 15px;
    border-radius: 50%;
    border: 1px solid #000;
    max-width: 42px;
  }
}

.mobile-add {
  display: none;

  .mobile_design {
    width: 35px;
  }
}

/*******************slider/.js**************/
.owl-carousel {
  margin-top: 90px;
  overflow: hidden;
  max-width: 177px;

  .item {
    background-color: #f7e611;
    max-width: 138px;
    height: 260px;
    position: relative;
    display: flex;
    flex-direction: column;

    p {
      font-size: 45px;
      color: #000;
      font-family: 'Giometria';
      font-weight: 800;
      font-style: italic;
      padding: 0px 44px;
      margin-top: 170px;
      margin-block-end: 2px;

    }

    p:last-child {
      margin-left: 47px;
      font-size: 17px;
      position: relative;
      margin-top: 0;
      margin-block-start: 2px;
      margin-block-end: 2px;
    }

    p:last-child:before {
      content: url("");
      position: absolute;
      background-color: #000;
      height: 2px;
      width: 10px;
      top: -16px;
    }
  }
}

.owl-carousel .owl-nav button.owl-next {
  position: absolute;
  top: 111px;
  left: 162px;
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
  font-size: 50px;
  color: #888235;

}

.owl-item>div {
  cursor: pointer;
  margin: 8% 6%;
  transition: margin 0.2s ease;
}

.owl-item.center>div {
  cursor: auto;
  margin: 0 auto;
}

.owl-carousel .owl-nav button.owl-prev {
  position: absolute;
  top: 111px;
  left: -3px;
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
  font-size: 50px;
  color: #888235;
}

.giometric_right {
  margin-left: 334px;
  margin-top: 36px;
  max-width: 100%;
}

.title {
  position: absolute;
  z-index: 7;
  left: 86px;
  top: 148px;
  font-size: 140px;
  color: #fff;
}



